/* tslint:disable:component-selector-name  component-selector-type */
import { Component, Input } from '@angular/core';
import { ColumnComponent } from './column.component';
import { DetailTemplateDirective } from './detail-template.directive';

/**
 * @hidden
 */
@Component({
    selector: '[kendoGridColGroup]',
    template: `
    <template [ngIf]="true">
        <col [class.k-hierarchy-col]="true" *ngIf="detailTemplate?.templateRef"/>
        <col *ngFor="let column of columns" [style.width.px]="column.width"/>
    </template>
    `
})
export class ColGroupComponent {
    @Input() public columns: Array<ColumnComponent> = [];
    @Input() public detailTemplate: DetailTemplateDirective;
}



